import {
	uniPopup
} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			title: "修改个人信息",
			SexArr: ['男', '女'],
			sexIndex: 0,
			person:{
				username:"", // username
				sex:'',
				mobile:"",
				nickname:"",
				headPic: '../../static/person/por.png'
				
			},
			oldData: {
				username: '',
				sex:'',
				mobile:"",
				nickname: ''
			}
		}
	},
	onUnload(){
		uni.hideLoading()
	},
	onShow(){
		this.init()
	},
	methods: {
		
		// 初始化
		init(){
			if(this.$store.state.userInfo.type){
				this.person.nickname = this.$store.state.userInfo.nickname
				this.person.username = this.$store.state.userInfo.username
				this.person.sex = this.$store.state.userInfo.sex
				this.person.mobile = this.$store.state.userInfo.mobile
				this.person.headPic = this.$store.state.userInfo.avatar
				this.person.nickname = this.$store.state.userInfo.nickname
			}
			let data = this.person
			this.oldData = Object.assign({}, data); 
		},
		
		// 修改密码
		goModifyPass(){
			uni.navigateTo({
				url:'../modify/modify'
			})
		},
		
		// 选择性别
		choseSex(e){
			// this.$refs.popup.open()
			
			this.person.sex = this.SexArr[e.target.value]
			this.sexIndex = e.target.value
			console.log(this.person.sex )
			
		},
		
		// 设置性别
		setSex(type){
			this.person.sex = type
			type == 0?this.person.sexStr = '男' : this.person.sexStr = '女',
			this.$refs.popup.close()
		},
		
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 退出登陆
		outsigin(){
			let _this = this
			uni.showModal({
				title: '操作提示',
				content: '是否退出当前账号？',
				success(res){
					if(res.confirm){
						_this.$store.commit("setUserInfo", {
							token: '', // token
							username: '点击登录', // 用户名
							avatar: '../../static/mine/notPor.png', // 头像
							score: 0,
							nickname: '',// 昵称 nickname
							type: '', // 登陆方式 1三方授权  2账号密码/验证码。
							mobile: '', // 手机号
							sex: '', //性别
							isSigin: false
						})
						// 清除本地缓存
						uni.removeStorage({
							key: 'srorage_token'
						})
						uni.removeStorage({
							key: 'srorage_nickname'
						})
						uni.removeStorage({
							key: 'srorage_username'
						})
						uni.removeStorage({
							key: 'srorage_avatar'
						})
						uni.removeStorage({
							key: 'srorage_score'
						})
						uni.removeStorage({
							key: 'srorage_sex'
						})
						uni.removeStorage({
							key: 'srorage_mobile'
						})
						uni.removeStorage({
							key: 'srorage_type'
						})
						// uni.removeStorage({
						// 	key: 'srorage_token'
						// })
						uni.removeStorage({
							key: 'srorage_token',
							success(res) {
								console.log('本地缓存清除完毕',res.data)
							}
						})
						
						uni.switchTab({
							url: '../mine/mine'
						})
					}else{
						return
					}
				}
			})
			
		},
		// 返回上级目录
		goBack(){
			if(this.oldData.username != this.person.username || this.oldData.sex != this.person.sex || this.oldData.nickname != this.person.nickname){
				let _this = this
				uni.showModal({
					title: '提示',
					content: '是否保存修改？',
					success(res){
						console.log(111)
						if(res.confirm){
							let userinfo = _this.$store.state.userInfo
							console.log('修改个人信息', userinfo)
							
							// 保存修改
							uni.request({
								url: _this.$http + '/api/user/changeInfo',
								method: 'POST',
								data:{
									token: userinfo.token,
									username: _this.person.username,
									nickname: _this.person.nickname,
									sex: _this.person.sex,
									user_id: userinfo.user_id
								},success(res){
									console.log('修改后返回数据',res)
									if(res.data.code == 1){
										
										_this.resetInfo(res.data.data)
										
										let obj = {
											nickname: _this.person.nickname,
											username: _this.person.username,
											sex: _this.person.sex 
										}
										
										_this.$store.commit('setUserSexInfo', obj)
										
										console.log('修改后vuex', _this.$store.state.userInfo)
										
										uni.navigateBack({})
										
										uni.showToast({
											title: '修改成功'
										})
										
									}else{
										uni.showModal({
											title: '提示',
											content: '修改失败'
										})
									}
									
								}
							})
							
						}else{
							uni.navigateBack({})
						}
					}
				})
			}else{
				uni.navigateBack({})
			}
			
		},
		// 更新修改后的数据
		resetInfo(info){
			// 更新store数据 nackname
			console.log('$store', userInfo)
			let userInfo = this.$store.state.userInfo
			console.log('info', info)
			userInfo.nickname = info.nickname
			userInfo.username = info.username
			userInfo.sex = info.sex
			this.$store.commit('setUserInfo', userInfo)
			
			// 更新状态storeage内数据
			uni.setStorage({
				key: 'srorage_sex',
				data: userInfo.sex
			})
			uni.setStorage({
				key: 'srorage_nickname',
				data: userInfo.nickname
			})
			uni.setStorage({
				key: 'srorage_username',
				data: userInfo.username
			})
		}
	},
	mounted() {
	},
	components: {
		uniPopup
	}
}
